.fb-table {
  table {
    @extend .table;
    margin-bottom: 0;
  }
  &.fb-table-bordered {
    table {
      @extend .table-bordered;
    }
  }
  &.fb-table-striped {
    table {
      @extend .table-striped;
    }
  }
  &.fb-table-hover {
    table {
      @extend .table-hover;
    }
  }
  &.fb-table-center {
    th, td {
      vertical-align: middle;
    }
  }

  p-tablecheckbox, p-tableheadercheckbox {
    position: relative;
    top: -1px;
  }
  .card & {
    margin-left: -1px;
    margin-right: -1px;
  }
}

.table-col-checkbox {
  width: 27px;
  text-align: center;
}

.table-col-number {
  width: 45px;
  text-align: center;
}

.ui-chkbox-box {
  border: 1px solid rgb(218, 221, 225);
  -webkit-border-radius: 2px;
  -moz-border-radius: 2px;
  border-radius: 2px;
  &.ui-state-default {

  }
  .ui-state-active {

  }
}

.ui-state-highlight,
.fb-table.fb-table-striped table tbody tr:nth-of-type(#{$table-striped-order}).ui-state-highlight {
  background-color: $table-active-bg;
  //background-color: #577fbc;
  color: #ffffff;
}

.fb-table.fb-table-striped table thead th.ui-state-highlight {
  color: $blue;
}

.ui-paginator-element {
  @extend .btn;
  @extend .btn-secondary;
  &.ui-state-active {
    @extend .btn-primary;
    position: relative;
    z-index: 2;
  }
}

.ui-state-disabled {
  opacity: $btn-disabled-opacity;
  background-image: none;
  @include box-shadow(none);
}

.ui-paginator {
  @include flexbox;
  @include justify-content(center);
  padding: $card-spacer-x;
  .ui-paginator-pages {
    margin: 0;
    @include font-size(0px);
  }
  .ui-paginator-element {
    padding-top: $btn-padding-y;
    padding-bottom: $btn-padding-y;
    -webkit-border-radius: 0;
    -moz-border-radius: 0;
    border-radius: 0;
    width: auto;
    height: auto;
    min-height: 28px;
    min-width: 28px;
    margin: 0 -1px 0 0;
    cursor: pointer;
    &.ui-paginator-first {
      @include border-left-radius(2px);
    }
    &.ui-paginator-last {
      @include border-right-radius(2px);
    }
  }
}

.ui-inputtext {
  @extend .form-control;
}

.ui-multiselect {
  @extend .custom-select;
  .ui-multiselect-label {
    margin: 0;
    padding: 0;
    white-space: nowrap;
  }
  .ui-multiselect-trigger-icon {
    visibility: hidden;
  }

  .ui-multiselect-panel {
    background-color: $dropdown-bg;
    padding: 0;
    label {
      margin: 0;
      cursor: pointer;
    }
    .ui-chkbox {
      position: relative;
      top: -1px;
    }
    .ui-multiselect-item {
      &:not(.ui-state-highlight):not(.ui-state-disabled):hover {
        background-color: rgba($ui-state-highlight, 0.5);
      }
    }
  }

  .ui-multiselect-header {
    @include flexbox;
    @include align-items(center);
    margin: 0;
    border-bottom: 1px solid $input-border-color;
    background-color: $custom-select-bg;
    .ui-multiselect-filter-container {
      @include flex(1);
      margin: 0 5px;
    }
    .ui-multiselect-filter-icon {
      @include center(false, true);
      left: $input-padding-x-sm;
    }
    .ui-inputtext {
      padding: $input-padding-y-sm $input-padding-x-sm $input-padding-y-sm $input-padding-x-sm*2+0.5rem;
      font-size: $font-size-sm;
      line-height: $input-line-height-sm;
      @include border-radius($input-border-radius-sm);
      display: block;
      width: 100%;
    }
    .ui-multiselect-close {
      position: relative;
      top: auto;
      right: auto;
      left: auto;
    }
  }
}

.ui-slider {
  background-color: #e6e6e6;
  border-color: #e6e6e6;

  &.ui-slider-horizontal .ui-slider-handle {
    top: -.2em;
  }
  &.ui-slider-vertical .ui-slider-handle {
    left: -.2em;
  }
  .ui-slider-handle {
    background-color: theme-color('primary');
    -moz-transition: background-color 0.3s;
    -o-transition: background-color 0.3s;
    -webkit-transition: background-color 0.3s;
    transition: background-color 0.3s;
    -moz-border-radius: 100%;
    -webkit-border-radius: 100%;
    border-radius: 100%;
  }
  &.ui-slider-animate .ui-slider-handle {
    -moz-transition: background-color 0.3s, left 0.3s;
    -o-transition: background-color 0.3s, left 0.3s;
    -webkit-transition: background-color 0.3s, left 0.3s;
    transition: background-color 0.3s, left 0.3s;
  }

  .ui-slider-handle:hover {
    background-color: #0267bf;
  }
  .ui-slider-range {
    background: $blue;
  }
}

p-sorticon {
  a {
    color: $blue;
    text-decoration: none;
  }
  .ui-sortable-column-icon {
    vertical-align: middle;
    position: relative;
    top: -1px;
  }
}

.ui-autocomplete {
  display: block;
  .ui-autocomplete-panel {
    background-color: $dropdown-bg;
  }
  .ui-autocomplete-input-token {
    display: block;
    input {
      width: 100%;
    }
  }
  .ui-autocomplete-token + .ui-autocomplete-input-token {
    display: inline-block;
    input {
      width: auto;
    }
  }
}
